#bx-soa-total{
	width: 440px;
}
.checkout_price{
	border: 1px solid #CFD5D5;
	width: 440px;
	border-radius: 5px;
	&__top{
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20px 35px;
		border-bottom: 1px solid #CFD5D5;
		span{
			font-weight: 600;
			font-size: 15px;
			line-height: 18px;
			text-transform: uppercase;
			color: #1F1F1F;
		}
		a{
			font-size: 15px;
			line-height: 18px;
			color: #F74B4B;
			&:hover{
				text-decoration: underline;
			}
		}
	}
	&__body{
		padding: 35px;
		border-bottom: 1px solid #CFD5D5;
		ul{
			li{
				display: flex;
				justify-content: space-between;
				margin-bottom: 15px;
				position: relative;
				&::before{
					content: '';
					width: 100%;
					height: 2px;
					border-bottom: 1px dotted #BEC5C5;
					position: absolute;
					left: 0;
					bottom: 5px;
					z-index: 1;
				}
				span{
					font-size: 15px;
					line-height: 18px;
					color: #1F1F1F;
					padding: 2px;
					background-color: #fff;
					position: relative;
					max-width: 45%;
					z-index: 10;
					&:nth-child(2){
						font-weight: 600;
					}
				}
			}
			.econom{
				padding: 8px 12px;
				background: #E0ECEC;
				border-radius: 5px;
				margin-top: 20px;
				margin-bottom: 0;
				&::before{
					content: none;
				}
				span{
					background: none;
				}
			}
		}
	}
	&__bottom{
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20px 35px;
		.total{
			font-weight: 600;
			font-size: 15px;
			line-height: 18px;
			color: #1F1F1F;
		}
	}
}